* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato';
  font-size: 18px;
  background-color: hsl(0, 0%, 98%);
  color: hsl(234, 12%, 34%);
  width: 100%;
  height: 100vh;
}

.header {
  background-color: hsl(234, 12%, 34%);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10px;
}

.nav__img {
  width: 50px;
}

.nav__img:hover {
  box-shadow: 0px 0px 10px 0px rgb(126, 135, 255);
}

.nav__list {
  list-style: none;
}

.nav__item {
  display: inline-block;
  margin: 0 5px;
}

.nav__link {
  color: white;
}

a {
  color: hsl(220, 100%, 15%);
}

.hero {
  min-width: 230px;
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  margin-top: 90px;
  padding: 15px;
}

.hero__title {
  font-weight: 300;
}

.hero__strong {
  font-weight: 600;
}

.hero__description {
  margin-top: 10px;
  font-weight: 300;
}

.hero__badge-codewar {
  margin-top: 20px;
  width: 100%;
  max-width: 400px;
}

.container {
  margin-top: 10px;
  padding: 0 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

a, a:visited, a:focus, a:active {
  text-decoration: none;
}

a:hover {
  color: rgb(223, 132, 132);  
}

.card-project {
  width: 340px;
  height: 200px;
  background-color: white;
  margin: 10px 10px;
  padding: 20px 15px;
  border-radius: 5px;
  border-bottom: 5px solid darkslategray;
  overflow: hidden;
  
}

.card-project__description {
  margin: 15px 0 20px;
  font-size: 15px;
  font-weight: 300;
}

@media screen and (min-width: 600px) {
  .nav {
    padding: 10px 40px;
  }
}